<template>
  <div class="head" v-if="b">
    <div class="close">
      <img src="../../assets/imges/close.png" alt="" @click="fn">
    </div>
    <div class="cont">
      <div class="left">
        <div class="img">
          <img src="http://gfs9.gomein.net.cn/T1VnKvB7_T1RCvBVdK.png" alt="">
        </div>
        <div class="text">
          <p>注册享好礼</p>
          <p>送360元礼包</p>
        </div>

      </div>
      <div class="right">
        下载领取
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "myTop",
        data(){
            return {
                b:true
            }
        },
        methods:{
            fn(){
                this.b=!this.b;
                this.$emit("xiha",this.b);
            }
        }
    }
</script>

<style scoped>
  /*头部*/
  .head {
    position: relative;
    height: .5rem;
    display: -webkit-flex;
    align-items: center;
    background: #000;
  }
  .head .close {
    margin-left: .15rem;
    width: .145rem;
    height: .145rem;
  }
  .head .cont {
    width: 100%;
    align-items: center;
    display: -webkit-flex;
    justify-content: space-between;
  }
  .head .cont .left {
    margin-left: .15rem;
    display: -webkit-flex;
  }
  .head .cont .left .img {
    margin-right: .1rem;
    width: .35rem;
    height: .35rem;
  }
  .head .cont .left .text {
    margin-top: .02rem;
  }
  .head .cont .left .text p {
    color: #fff;
    font-size: .12rem;
  }
  .head .cont .left .text p:nth-of-type(2) {
    color: #999;
  }
  .head .cont .right {
    width: .869rem;
    height: .5rem;
    background: #EE1955;
    color: #fff;
    text-align: center;
    line-height: .5rem;
    font-size: .15rem;
  }
</style>



