<template>
  <div class="slide">
    <div class="swiper-container" id="mySlide">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item,index) in list">
          <img :src="item.src" alt="">
        </div>
      </div>
      <!-- 如果需要分页器 -->
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
  import "@/assets/js/swiper.css";
  import Swiper from "swiper";
    export default {
        name: "silde",
        data(){
            return {
              list:[
                  {src:"https://gfs5.gomein.net.cn/wireless/T1S8LXBQx_1RCvBVdK_1080_320.jpg"},
                  {src:"http://gfs10.gomein.net.cn/wireless/T1UEKXBXAT1RCvBVdK_1080_320.jpg"},
                  {src:"http://gfs10.gomein.net.cn/wireless/T1LFhXBmL_1RCvBVdK_1080_320.png"},
                  {src:"http://gfs12.gomein.net.cn/wireless/T1EZDXBsxT1RCvBVdK_1080_320.jpg"},
                  {src:"http://gfs11.gomein.net.cn/wireless/T1cjZsBgxv1RCvBVdK_1080_320.jpg"},
                  {src:"http://gfs8.gomein.net.cn/wireless/T1FVDXBKhT1RCvBVdK_1080_320.jpg"},
                  {src:"http://gfs6.gomein.net.cn/wireless/T1TZEXBjZv1RCvBVdK_1080_320.jpg"},
                  {src:"http://gfs10.gomein.net.cn/wireless/T1TZZXB5Wv1RCvBVdK_1080_320.jpg"}
              ]
            }
        },
        mounted(){
            var mySwiper = new Swiper ('#mySlide', {
                direction: 'horizontal', // 水平切换选项
                loop: true, // 循环模式选项
                autoplay: {
                    disableOnInteraction: false,
                },
                // 如果需要分页器
                pagination: {
                    el: '.swiper-pagination',
                },
                observer:true,
                observeParents:true
            })
        }
    }
</script>

<style scoped>
  .slide{
    width:100%;
    height:1.1rem;
  }
</style>



