<template>
    <div :class="fixed">
        <div class="left">
            <slot name="one">
              <img src="https://css.gomein.net.cn/plus/style/index/images/grass_logo.png?v=2017092001" alt="">
            </slot>
        </div>
        <div class="center">
          <slot name="input">
            <input type="text" placeholder="香肠派对">
          </slot>
        </div>
        <div class="next">
          <slot name="two">
            <img src="../../assets/imges/goshop.png" alt="">
          </slot>
        </div>
        <div class="right">
          <slot name="three">
            <div>
              登录
            </div>
          </slot>
        </div>
    </div>
</template>

<script>
    export default {
        name: "myHead",
        data(){
            return {
               fixed:"search"
            }
        },
        mounted(){
            var that = this;
           window.onscroll=function(){
              if(window.scrollY == 0){
                  that.fixed="search"
              }
              else{
                  that.fixed="search fix"
              }
           }
        }
    }
</script>

<style scoped>
  .search {
    background: #fff;
    height: .45rem;
    display: -webkit-flex;
    justify-content: space-around;
    align-items: center;
    top: 0;
    width: 100%;
    border-bottom: #ccc 1px solid;
  }
  .search .left {
    width: .24rem;
    height: .24rem;
  }
  .search .center {
    width: 2.23rem;
    height: .3rem;
  }
  .search .center input {
    width: 100%;
    height: 100%;
    border-radius: .15rem;
    text-indent: .2rem;
    border: none;
    background: #f6f6f6;
    outline: none;
  }
  .search .next {
    width: .22rem;
    height: .195rem;
  }
  .search .right {
    font-size: .16rem;
  }
  .fix {
    position: fixed;
    z-index: 9999;
  }

</style>



