<template>
    <div class="mine">
      <router-link to="/" class="mine-top"><</router-link>
      <div class="mine-one">
        <img src="https://misc.gomein.net.cn/plus/images/u/login/logo_xh.png?v=20190624" alt="">
      </div>
      <div class="mine-two">
        <div class="mine-input1">
          <div @click="b=true">账号密码登录</div>
          <div @click="b=false">短信验证码登录</div>
        </div>
        <div v-if="b">
          <div class="mine-input2">
            <input type="text" placeholder="手机号/邮箱/用户名/门店会员卡号">
          </div>
          <div class="mine-input3">
            <input type="text" placeholder="请输入密码">
          </div>
          <div class="mine-input4">
            <div>手机号快速注册</div>
            <div>找回密码</div>
          </div>
        </div>

        <div style="position:relative;" v-else>
          <div class="mine-input2">
            <input type="text" placeholder="手机号">
          </div>
          <div class="mine-input3">
            <input type="text" placeholder="请输入密码">
          </div>
          <div style="position:absolute;width:1rem;height:.32rem;line-height:.32rem;text-align:center;border: 1px solid #F20C59;color:#F20C59;border-radius:.16rem;top:.5rem;right:0;">
            获取验证码
          </div>
        </div>

        <div class="mine-input5">
          登录
        </div>
      </div>
      <div class="mine-three">
        <div class="mine-end-title">
            <div class="mine-end-txt">使用以下账户登录</div>
            <div class="mine-end-line"></div>
        </div>
        <div class="mine-end-cont">
          <div>
            <img src="https://misc.gomein.net.cn/plus/images/u/login_new/mdhy.png" alt="">
            <p>门店会员</p>
          </div>
          <div>
            <img src="https://misc.gomein.net.cn/plus/images/u/login_new/QQ.png" alt="">
            <p>QQ</p>
          </div>
          <div>
            <img src="https://misc.gomein.net.cn/plus/images/u/login_new/weibo.png" alt="">
            <p>新浪微博</p>
          </div>
          <div>
            <img src="https://misc.gomein.net.cn/plus/images/u/login_new/zfb.png" alt="">
            <p>支付宝</p>
          </div>
        </div>
      </div>

    </div>
</template>

<script>
    export default {
        name: "Mine",
        data(){
          return {
              b:true
          }
        },
        mounted() {
            var divs = document.getElementsByClassName("mine-input1")[0].getElementsByTagName("div");
            for(var i=0;i<divs.length;i++){
                divs[i].onclick=function(){
                    for(var j=0;j<divs.length;j++){
                        divs[j].style.color="#000";
                        divs[j].style.border="none";
                    }
                    this.style.color="#FC337D";
                    this.style.borderBottom="#FC337D 3px solid";
                }
            }
        }

    }
</script>

<style scoped>
  .mine {
    padding: 0 .425rem .425rem;
  }
  .mine-top {
    height: .44rem;
    line-height: .44rem;
    font-size: .2rem;
    position: absolute;
    top: 0;
    left: .12rem;
  }
  .mine-one {
    width: 1.37rem;
    height: .45rem;
    margin: .425rem auto .1rem;
  }
  .mine-two div {
    margin-top: .1rem;
  }
  .mine-two .mine-input1 {
    display: -webkit-flex;
    justify-content: space-around;
    height: .428rem;
    margin-bottom: .25rem;
    align-items: center;
  }
  .mine-two .mine-input1 div {
    line-height: .428rem;
    flex: 1;
    text-align: center;
  }
  .mine-two .mine-input1 div:nth-of-type(1) {
    color: #FC337D;
    border-bottom: #FC337D 3px solid;
  }
  .mine-two .mine-input2 {
    width: 100%;
    height: .42rem;
    line-height: .42rem;
    border-bottom: 2px #ccc solid;
  }
  .mine-two .mine-input2 input {
    border: none;
    outline: none;
    width: 100%;
    height: 100%;
    color: #ccc;
    text-indent: .05rem;
  }
  .mine-two .mine-input3 {
    width: 100%;
    height: .42rem;
    line-height: .42rem;
    border-bottom: 2px #ccc solid;
  }
  .mine-two .mine-input3 input {
    border: none;
    outline: none;
    width: 100%;
    height: 100%;
    color: #ccc;
    text-indent: .05rem;
  }
  .mine-two .mine-input4 {
    margin-top: .2rem;
    display: -webkit-flex;
    justify-content: space-between;
    font-size: .15rem;
    color: #A9D6F1;
  }
  .mine-two .mine-input5 {
    margin-top: .3rem;
    height: .44rem;
    line-height: .44rem;
    border-radius: .22rem;
    background: #FF87B5;
    color: #fff;
    text-align: center;
  }
  .mine-three {
    width: 77%;
    position: fixed;
    bottom: .6rem;
  }
  .mine-three .mine-end-title {
    position: relative;
    height: .2rem;
  }
  .mine-three .mine-end-title .mine-end-txt {
    width: 1.5rem;
    text-align: center;
    background: #fff;
    position: absolute;
    left: 50%;
    margin-left: -0.75rem;
    z-index: 12;
  }
  .mine-three .mine-end-title .mine-end-line {
    width: 100%;
    height: .01rem;
    background: #ccc;
    position: absolute;
    top: .1rem;
  }
  .mine-three .mine-end-cont {
    margin-top: .2rem;
    display: -webkit-flex;
    justify-content: space-between;
  }
  .mine-three .mine-end-cont div {
    width: .5rem;
  }
  .mine-three .mine-end-cont div img {
    width: .42rem;
    height: .42rem;
    margin-left: .04rem;
  }
  .mine-three .mine-end-cont div p {
    text-align: center;
    font-size: .12rem;
  }
</style>



