<template>
  <div class="footer">
    <div class="footer-box" v-for="(item,index) in list">
      <router-link :to="item.a">
        <img :src="item.src" alt="">
        <p class="footer-p">{{item.p}}</p>
      </router-link>
    </div>
  </div>
</template>

<script>
    export default {
        name: "myFoot",
        data(){
            return {
                list:[
                    {p:"首页",src:"./static/image/41.png",a:"/"},
                    {p:"分类",src:"./static/image/42.png",a:"/Classify"},
                    {p:"优选",src:"./static/image/43.png",a:"/Priority"},
                    {p:"购物车",src:"./static/image/44.png",a:"/Trolley"},
                    {p:"我的",src:"./static/image/45.png",a:"/Mine"}
                ]
            }
        },
        mounted(){
            var p = document.getElementsByClassName("footer-p")[0];
            p.style.color="#ff0027";
        }
    }
</script>

<style scoped>
  .footer {
    height: .46rem;
    border-top: #ccc 1px solid;
    display: -webkit-flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    bottom: 0;
    width: 100%;
    background: #fff;
    z-index: 9999;
  }
  .footer a {
    display: block;
  }
  .footer a img {
    width: .24rem;
    height: .24rem;
  }
  .footer a p {
    text-align: center;
    font-size: .1rem;
  }
</style>



